___CSS_LOADER_EXPORT___.push([module.id, `main > .container {
    padding: 70px 15px 20px;
}

.footer {
    background-color: #f5f5f5;
    font-size: .9em;
    height: 60px;
}

.footer > .container {
    padding-right: 15px;
    padding-left: 15px;
}

.not-set {
    color: #c55;
    font-style: italic;
}

/* add sorting icons to gridview sort links */
a.asc:after, a.desc:after {
    content: '';
    left: 3px;
    display: inline-block;
    width: 0;
    height: 0;
    border: solid 5px transparent;
    margin: 4px 4px 2px 4px;
    background: transparent;
}

a.asc:after {
    border-bottom: solid 7px #212529;
    border-top-width: 0;
}

a.desc:after {
    border-top: solid 7px #212529;
    border-bottom-width: 0;
}

.grid-view th,
.grid-view td:last-child {
    white-space: nowrap;
}

.grid-view .filters input,
.grid-view .filters select {
    min-width: 50px;
}

.hint-block {
    display: block;
    margin-top: 5px;
    color: #999;
}

.error-summary {
    color: #a94442;
    background: #fdf7f7;
    border-left: 3px solid #eed3d7;
    padding: 10px 20px;
    margin: 0 0 15px 0;
}

/* align the logout "link" (button in form) of the navbar */
.nav li > form > button.logout {
    padding-top: 7px;
    color: rgba(255, 255, 255, 0.5);
}

@media(max-width:767px) {
    .nav li > form > button.logout {
        display:block;
        text-align: left;
        width: 100%;
        padding: 10px 0;
    }
}

.nav > li > form > button.logout:focus,
.nav > li > form > button.logout:hover {
    text-decoration: none;
    color: rgba(255, 255, 255, 0.75);
}

.nav > li > form > button.logout:focus {
    outline: none;
}

.control-panel ul {
	padding-left: 0;
	list-style-type: none;
}

.control-panel ul ul li {
	margin-left: 1em;
	font-size: 0.8em;
	list-style-type: disc;
}

.form-group {
	margin: 0;
	padding: 1em 0;
}

.control-group + .control-group {
	margin-top: 1em;
}

.row h4 {
	padding: 0;
}

.row .col-sm-6 {
	padding: 0;
}

.row .col-sm-6:first-child {
	padding-left: 0;
}

.row .col-sm-6 + .col-sm-6 {
	padding-left: 1em;
}

.form-group input + label,
.form-group select + label,
.form-group button + label {
	margin-top: 1em;
}

.form-group button,
.form-group a.btn {
	margin-top: 1em;
}

ul.pagination li {
	margin: 0 0.5em;
}

ul.pagination li.active a {
	color: #fff;
	background-color: #198754;
	text-decoration: none;
}

div.help-block {
	color: red;
	font-size: 0.8em;
}

.languages-switching a {
	color: #ccc;
	text-decoration: none;
}

.languages-switching .language-active a {
	color: red;
}

.languages-switching span:first-child::before {
	content: '';
	color: white;
}

.languages-switching span::before {
	content: '|';
	color: white;
}

.languages-container {
	display: inline-block;
	padding: 0 0.5em;
	background-color: #0d6efd;
	border-radius: 0.5em;
}
